.pp-about-component {
  background: #2B2B2B;
  color: #A8A8A8; }
  .pp-about-component h3 {
    color: #ffffff; }
  .pp-about-component img {
    width: 100%;
    max-width: 200px;
    max-height: 200px;
    border-radius: 50%; }
  .pp-about-component .pp-description {
    font-family: 'librebaskerville-italic', serif;
    text-align: justify;
    margin: 0 0 20px 0; }

@media only screen and (max-width: 601px) {
  .pp-download-cv-btn {
    display: block; } }



