#nav-wrap ul, #nav-wrap li, #nav-wrap a {
  margin: 0;
  padding: 0;
  border: none;
  outline: none; }

/* nav-wrap */
#nav-wrap {
  font: 12px 'opensans-bold', sans-serif;
  width: 100%;
  text-transform: uppercase;
  letter-spacing: 2.5px;
  margin: 0 auto;
  position: fixed;
  left: 0;
  top: 0;
  background-color: rgba(225, 225, 225, 0.4); }

.opaque {
  background-color: #333 !important; }
  .opaque ul#nav li a {
    color: #ffffff; }

/* hide toggle button */
#nav-wrap > a.mobile-btn {
  display: none; }

ul#nav {
  min-height: 48px;
  width: auto;
  /* center align the menu */
  text-align: center; }

ul#nav li {
  position: relative;
  list-style: none;
  height: 48px;
  display: inline-block; }

/* Links */
ul#nav li a {
  font-weight: bold;
  /* 8px padding top + 8px padding bottom + 32px line-height = 48px */
  display: inline-block;
  padding: 8px 13px;
  line-height: 32px;
  text-decoration: none;
  text-align: left;
  color: #26a69a;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.2);
  -webkit-transition: color .2s ease-in-out;
  -o-transition: color .2s ease-in-out;
  transition: color .2s ease-in-out; }

ul#nav li a:active {
  background-color: transparent !important; }

ul#nav li.current a {
  color: #26a69a; }

@media only screen and (max-width: 992px) {
  /* mobile navigation
  -------------------------------------------------------------------- */
  #nav-wrap {
    font: 12px 'opensans-bold', sans-serif;
    background: transparent !important;
    letter-spacing: 1.5px;
    width: auto;
    position: fixed;
    top: 0;
    right: 0; }
  #nav-wrap > a {
    width: 48px;
    height: 48px;
    text-align: left;
    background-color: #26a69a;
    position: relative;
    border: none;
    float: right;
    font: 0/0 a;
    text-shadow: none;
    color: transparent;
    position: relative;
    top: 0px; }
  #nav-wrap > a:before,
  #nav-wrap > a:after {
    position: absolute;
    border: 2px solid #fff;
    top: 35%;
    left: 25%;
    right: 25%;
    content: ''; }
  #nav-wrap > a:after {
    top: 60%; }
  /* toggle buttons */
  #nav-wrap:not(:target) > a:first-of-type,
  #nav-wrap:target > a:last-of-type {
    display: block; }
  /* hide menu panel */
  #nav-wrap ul#nav {
    height: auto;
    display: none;
    clear: both;
    width: auto;
    float: right;
    position: relative;
    top: 0;
    right: 0; }
  /* display menu panels */
  #nav-wrap:target > ul#nav {
    display: block;
    padding: 20px;
    background: #1f2024;
    clear: both; }
  ul#nav li {
    display: block;
    height: auto;
    margin: 0 auto;
    padding: 0 4%;
    text-align: left;
    border-bottom: 1px solid #2D2E34;
    border-bottom-style: dotted; }
  ul#nav li a {
    color: #ffffff;
    display: block;
    padding: 12px 0;
    line-height: 16px;
    /* reset line-height from 48px */
    border: none;
    text-align: center; } }

@media only screen and (max-width: 601px) {
  #nav-wrap ul#nav {
    width: auto;
    float: none; } }



