.pp-footer-component {
  position: relative;
  background: #0f0f0f;
  padding: 28px 0;
  color: #343434; }
  .pp-footer-component a {
    color: #525252; }
    .pp-footer-component a:hover {
      color: #26a69a; }
  .pp-footer-component .pp-social-links-container {
    padding: 14px 0; }
  .pp-footer-component .pp-copyright {
    margin: 0;
    padding: 0; }
    .pp-footer-component .pp-copyright li {
      display: inline;
      margin: 0;
      padding: 0;
      line-height: 24px; }
      .pp-footer-component .pp-copyright li:before {
        content: "\2022";
        padding-left: 10px;
        padding-right: 10px;
        color: #26a69a; }
      .pp-footer-component .pp-copyright li:first-child:before {
        display: none; }
  .pp-footer-component .pp-scrollbtn-container {
    position: absolute;
    top: -28px;
    left: 50%;
    margin-left: -28px; }



