.pp-resume-component h3 {
  color: #2B2B2B; }

.pp-resume-component .pp-resume-level {
  border-bottom: 3px solid #26a69a;
  font: 16px/24px 'Roboto', serif;
  font-weight: bold;
  color: #2B2B2B;
  text-transform: uppercase;
  letter-spacing: 1px; }

.pp-resume-component .pp-resume-item {
  padding-top: 12px; }
  .pp-resume-component .pp-resume-item h4 {
    font: 26px/24px 'Roboto', serif;
    font-weight: bold;
    letter-spacing: 1px;
    margin: 8px 0;
    color: #000000; }
    .pp-resume-component .pp-resume-item h4 a {
      color: #000000; }
      .pp-resume-component .pp-resume-item h4 a i {
        font-size: 20px; }
      .pp-resume-component .pp-resume-item h4 a:hover {
        color: #26a69a; }
  .pp-resume-component .pp-resume-item p {
    color: #6E7881;
    margin: 0; }
  .pp-resume-component .pp-resume-item p.pp-info {
    font: 16px/24px 'librebaskerville-italic', serif;
    margin-bottom: 12px; }
    .pp-resume-component .pp-resume-item p.pp-info .pp-date {
      font: 12px/24px 'librebaskerville-italic', serif; }
  .pp-resume-component .pp-resume-item p.pp-desc {
    font-family: 'Roboto', serif;
    font-weight: 300;
    text-align: justify;
    font-size: small; }

@media only screen and (max-width: 601px) {
  .pp-resume-component .pp-resume-level {
    text-align: center; } }



